import React from "react";
import { BoxDonation } from "../../molecules/BoxDonation/BoxDonation";
import { graphql, useStaticQuery } from "gatsby";
import HeroSection from "../../molecules/HeroSection/HeroSection";
import { AboutSection } from "../../organisms/AboutSection/AboutSection";
import { VisionOfLadieJoy } from "../../organisms/visionOf/visionOf";
import SectionActions from "../../organisms/ActionsSection/ActionsSection";
import { useAboutContentQuery } from "../../hooks/useAboutContentQuery";

const QuiSommesNousPageWrapper = ({ content }) => {
  const { actions } = content;
  const { desktop } = useStaticQuery(graphql`
    query QuiSommesNous {
      desktop: file(relativePath: { eq: "about/hero-about.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);
  const contentData = useAboutContentQuery();
  const heroContent = contentData.content.edges[0].node.frontmatter.content.heroContent;
  const visionContent = contentData.content.edges[0].node.frontmatter.content.visionContent;
  return (
    <>
      <BoxDonation />
      <HeroSection image={desktop.childImageSharp.fluid}>
        <div className="heroInfo d-center">
          <h1 className="heading-standard hero-title-center">
            PLUS PROCHE DES FEMMES <br /> ET DES JEUNES FILLES
          </h1>
        </div>
      </HeroSection>
      <AboutSection descriptions={heroContent.description} />
      <VisionOfLadieJoy data={visionContent} />
      <SectionActions actions={actions} btnText={"Je deviens adhrent"} extraClassName="hero-title-center text-c" link="/je-deviens-adherent/" />
    </>
  );
};

export default QuiSommesNousPageWrapper;
