import { graphql, useStaticQuery } from "gatsby";

export const useAboutContentQuery = () => {
  const data = useStaticQuery(graphql`
    query MyContentAboutQuery {
      content: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "/content/about-page-content/" } }
      ) {
        edges {
          node {
            id
            frontmatter {
              content {
                heroContent {
                  description
                  title
                  Image {
                    childImageSharp {
                      gatsbyImageData(
                        height: 250
                        blurredOptions: { width: 200 }
                        placeholder: BLURRED
                        transformOptions: { cropFocus: CENTER }
                        aspectRatio: 1.5
                      )
                    }
                  }
                }
                visionContent {
                  description
                  title
                }
              }
            }
          }
        }
      }
    }
  `);
  return data;
};
