import React from "react";
import parse from "html-react-parser";
import "./visionof-style.scss";
export const VisionOfLadieJoy = ({ data }) => {
  return (
    <section className="visionHolder">
      <div className="container">
        <h1 className="hero-title-center text-c">{data.title}</h1>
        <div className="visionHolder-content">
          {data.description.map((item, index) => {
            return (
              <p key={index} className="p-vertical">
                {parse(item)}{" "}
              </p>
            );
          })}
        </div>
      </div>
    </section>
  );
};
